<template>
	<div
		class="zc-nav-item text-center relative"
		@click="navClick( propNavIndex )"
		:class="{ active: propNavIndex === propCurrentNav }"
	>
		<div class="zc-nav-border zc-nav-border-left absolute"></div>
		<p>{{ propNavItem }}</p>
	</div>
</template>

<script>
	export default {
		name: 'NavItem',
		props: ['propNavItem','propNavIndex','propCurrentNav'],
		data() {
			return {
				currentNavItem: this.propCurrentNav
			}
		},
		methods: {
			navClick( navClicked ) {
				this.currentNavItem = navClicked;
				this.$emit('emitCurrentNav', this.currentNavItem);
			}
		}
	}
</script>