<template>
		<transition
			name="fade"
		>
			<div
				class="modal absolute"
				v-if="sendModalActive"
			>
				<div class="container">
					<div class="row df jcc">
						<div class="modal-inner relative">
							<div
								class="modal-close absolute"
								@click="closeModal"
							>
								<img width="" src="img/modal-close.png">
							</div>
							<img
								class="modal-img"
								:src="sendModalImg"
							>
						</div>

					</div>
				</div>
			</div>
		</transition>
</template>

<script>
export default {
	name: 'Modal',
	props: ['sendModalActive', 'sendModalImg'],
	data() {
		return {
			modalActive: this.sendModalActive
		}
	},
	methods: {
		closeModal() {
			this.modalActive = false;
			this.$emit('modalClose', this.modalActive);
		}
	}
}
</script>

