<template>

	<div
		class="zc-item mb-20 relative"
		@click="clickedCard"
	>

		<div class="zc-item-card relative">
			<div class="zc-item-card-text df fdc jca">
				<div class="zc-item-card-label">
					<p>{{ propIndex.replace('-', ' ') }}</p>
				</div>
				<div class="zc-item-card-caption">
					<p>{{ propCard }}</p>
				</div>
			</div>

			<div class="zc-item-card-bottom absolute" :class="propIndex"></div>
		</div>
		<div>
			<img class="zc-item-img" :src="propImgUrl">
		</div>
	</div>


</template>

<script>
	export default {
		name: "Card",
		props: ["propCard", "propIndex", "propImgUrl"],
		data() {
			return {
				activateModal: true,
			}
		},
		methods: {
			clickedCard() {
				this.$emit('emitModalActivate', this.activateModal );
				this.$emit('emitModalImage', this.propImgUrl );
			}
		}

	}
</script>